/** @jsx jsx */
import { jsx } from '@emotion/core';
import { ReactNode } from 'react';
import { useSiteMetadata } from 'hooks/useSiteMetadata';
import { GlobalContext } from 'stores';

interface Props {
  children: ReactNode;
  pageContext: {
    lang: string | null;
  };
}
const PageRoot = ({ children, pageContext }: Props) => {
  const { defaultLanguage } = useSiteMetadata();
  return (
    <GlobalContext.Provider value={{ lang: pageContext.lang ?? defaultLanguage }}>{children}</GlobalContext.Provider>
  );
};

export default PageRoot;
