/** @jsx jsx */
import { jsx } from '@emotion/core';
import Helmet from 'react-helmet';
import { useStaticQuery, graphql } from 'gatsby';
import { ComponentProps } from 'react';

interface Props extends ComponentProps<typeof Helmet> {
  description?: string;
  lang?: string;
  image?: string;
}

const SEO = ({ description = '', lang = 'ko', meta = [], title, image }: Props) => {
  const { site, avatar } = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            title
            description
          }
        }
        avatar: file(absolutePath: { regex: "/me.jpg/" }) {
          childImageSharp {
            fixed(width: 640, height: 640) {
              ...GatsbyImageSharpFixed
            }
          }
        }
      }
    `
  );

  const defaultTitle = site.siteMetadata.title;
  const metaDescription = description ?? site.siteMetadata.description;
  const metaImage = image ?? avatar.childImageSharp.fixed.src;
  const defaultMeta = [
    {
      name: `description`,
      content: metaDescription,
    },
    {
      property: `og:title`,
      content: title,
    },
    {
      property: `og:description`,
      content: metaDescription,
    },
    {
      property: `og:image`,
      content: metaImage,
    },
    {
      property: `og:type`,
      content: `website`,
    },
  ];

  return (
    <Helmet
      htmlAttributes={{
        lang,
      }}
      defaultTitle={defaultTitle}
      title={title}
      titleTemplate={`%s | ${site.siteMetadata.title}`}
      meta={[...defaultMeta, ...meta]}
    />
  );
};

export default SEO;
